import React, { useContext } from "react"
import { PageLayout, PageTitle } from "../components"
import { Container, Image } from "react-bootstrap"
import { Link, graphql } from "gatsby"
import { ThemeContext, SEO } from "../utils"

import booksList from "../../static/mydata/books.json" 
import showsList from "../../static/mydata/tvshows.json" 
import moviesList from "../../static/mydata/movies.json" 

export default ({ data }) => {
  const BookLink = ({ title, author, link }) => (
    <li key={title} style={{ color: "gray" }}>
      <a rel="noopener noreferrer" href={link}>
        {title}
      </a>
      &nbsp;-<i>{author}</i>
    </li>
  )

  const MediaLink = ({ title, author, link }) => (
    <li key={title} style={{ color: "gray" }}>
      <a rel="noopener noreferrer" href={link}>
        {title}
      </a>
    </li>
  )

  const {
    author,
    occupation,
    designations,
    unemployed,
  } = data.site.siteMetadata
  const { toString } = useContext(ThemeContext)

  const books = booksList.map(book => BookLink(book));
  const shows = showsList.map(show => MediaLink(show))
  const movies = moviesList.map(movie => MediaLink(movie))

  return (
    <PageLayout>
      <SEO title="About Me" />
      <PageTitle title="About Me" />
      <Container>
        <Image
          roundedCircle
          width="340"
          height="340"
          src={`../../mydata/profilePicture.jpg`}
          alt={author}
        />
        <article className="w-75 m-auto pt-2 text-justify">
          <p className="text-center">
            {designations.map((attr, i) => (
              <span key={attr}>
                &nbsp;<b>{attr}</b>&nbsp;
                {i < designations.length - 1 && <>||</>}
              </span>
            ))}
          </p>
          <p className="i-5 mt-4 pt-2">
            Hello there! My name is <b>{`${author}`}</b>. I am a computer science student current waiting for my MSc Media Informatics course to start in RWTH Aachen.
          </p>
          <p className="i-5">
            In my spare time you will most likely find me on local trails or wildlife hotspots with my camera. Occasionally, I read books or play some musical instruments....
          </p>
          <p className="i-5">
            Check out my <Link to="/projects">projects</Link> to see what I've
            been up to! Or check out my <Link to="/blog">blog</Link> to see
            what's recently caught my eye!
          </p>
        </article>
        <article className="w-75 m-auto">
          {unemployed && (
            <>
              <hr />
              <p className="unemployed">
                <small>
                  I am <b>currently looking for new opportunities</b>! If you
                  like what you <Link to="/resume">see</Link>, let's get
                  in&nbsp;
                  <a
                    href="mailto:souruly@gmail.com"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    touch
                  </a>
                  !
                </small>
              </p>
            </>
          )}
          <hr />

          {/* <h5 className="watch-list-title pt-4">
            Here are the last 3 books I've read:
          </h5>
          <ul style={{ fontSize: "0.9rem", listStyle: "none" }}>{books}</ul>
          <h5 className="watch-list-title pt-4">
            Here are the last 3 TV Shows I've watched::
          </h5>
          <ul style={{ fontSize: "0.9rem", listStyle: "none" }}>{shows}</ul>
          <h5 className="watch-list-title pt-4">
           Here are the last 3 Movies I've watched::
          </h5>
          <ul style={{ fontSize: "0.9rem", listStyle: "none" }}>{movies}</ul>
         */}
        </article>
      </Container>
    </PageLayout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        unemployed
        occupation
        author
        designations
      }
    }
  }
`
